.Tweets_container {
    padding: 0 80px;
}

.Tweets_textBox {
    display: flex;
    align-items:center;
}

.Tweets_backBtn {
    padding: 5px;
    text-decoration: none;
    color: gray;
}

.Tweets_filter {
    margin-left: 30px;
}

.Tweets_title {
    color: #5736A3;
    font-family: 'Montserrat';
    font-weight: 500;
    font-size: 36px;
    margin-left: 30%;
    margin-right: 30%;
}

.Tweets_loadmore {
    display: block;
    margin-top: 40px;
    margin-left: calc(50% - 196px) ;
    width: 196px;
    padding: 14px;
    font-family: 'Montserrat';
    font-weight: 600;
    font-size: 18px;
    line-height: 1.22;
    text-transform: uppercase;
    color: #373737;
    border-radius: 10px;
    background: #EBD8FF;
    box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.25);
    border: none;
}

.Tweets_listGrid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 3em;
    list-style: none;
    padding: 0;
}